body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main{display:grid;grid-template-areas:". header ." "myContent myContent myContent" ". footer .";grid-template-rows:1fr 11fr 1fr;grid-template-columns:1fr 8fr 1fr;height:100vh;margin:0;background-image:url(/project/static/media/main.f6b65cd6.jpg);background-repeat:no-repeat;background-size:cover}.main .body{grid-area:myContent}.main .body .cards{display:grid;grid-template-rows:40% 40%;grid-template-columns:20% 20% 20%;height:100%;grid-gap:20px;justify-content:space-around;align-content:space-around}.main .body .cards .card{background:aqua}.main .header{display:grid;grid-template-areas:'text link';grid-area:header;background:green;grid-template-columns:10% 10%;justify-content:space-between}.main .header .text{grid-area:text}.main .header .link{grid-area:link}.main .footer{grid-area:footer;background:blue}@media all and (max-width: 500px){grid-template-areas:"header" "body" "footer";grid-template-rows:60px 1fr 60px;grid-template-columns:10% 1fr 10%}

.ticTacToe{grid-area:myContent;display:grid;grid-template-areas:'choose game choose';grid-template-rows:1fr;grid-template-columns:1fr 1fr 1fr;grid-gap:1em;justify-items:center;align-items:center}.ticTacToe .game{display:grid;grid-template-areas:'winner' 'board' 'alert';grid-template-rows:1fr 9fr 1fr;justify-items:center;align-items:center;grid-gap:2px}.ticTacToe .game .winner{grid-area:winner;display:grid;grid-template-areas:'text recycle';grid-template-columns:3fr 1fr;grid-template-rows:1fr;align-self:stretch;justify-self:stretch}.ticTacToe .game .winner .text{grid-area:text;font-size:25px;font-weight:bold;font-family:sans-serif;color:white}.ticTacToe .game .winner .recycle{grid-area:recycle;background-image:url(/project/static/media/recycle.c3b390de.png);background-size:contain;background-repeat:no-repeat;background-position:right}.ticTacToe .game .board{display:grid;grid-area:board;height:100%;margin:0;grid-template-rows:150px 150px 150px;grid-template-columns:150px 150px 150px}.ticTacToe .game .board .square{border:4px solid #ffffff}.ticTacToe .game .alert{grid-area:alert;font-size:25px;font-weight:bold;align-self:center;font-family:sans-serif;color:white}.steve{background-image:url(/project/static/media/steve.9b0f89f1.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.zombie{background-image:url(/project/static/media/zombie.2b4cf4f9.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.cow{background-image:url(/project/static/media/cow.772ae131.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.creeper{background-image:url(/project/static/media/creeper.c4c8a342.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.enderman{background-image:url(/project/static/media/enderman.85605244.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.farmer{background-image:url(/project/static/media/farmer.e91bb960.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.sheep{background-image:url(/project/static/media/sheep.6143437c.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.skeleton{background-image:url(/project/static/media/skeleton.b9adc7d8.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}.spider{background-image:url(/project/static/media/spider.fffe12e0.svg);background-size:90%;background-repeat:no-repeat;background-position:center;z-index:2}

.characterSelection{display:grid;grid-template-areas:'text' 'characters';grid-template-rows:10% 90%;grid-template-columns:100%;grid-gap:2px}.characterSelection .text{grid-area:text;font-size:25px;font-weight:bold;align-self:center;font-family:sans-serif;color:white}.characterSelection .characters{grid-area:characters;display:grid;height:100%;margin:0;grid-template-rows:150px 150px 150px;grid-template-columns:150px 150px 150px}.characterSelection .characters .character{border:4px solid #ffffff}.characterSelection .characters .selected{border:4px solid red}

.checkers{grid-area:myContent;display:grid;grid-template-rows:1fr;grid-template-columns:1fr 1fr 1fr;grid-gap:1em;justify-items:center;align-items:center}.checkers .game{display:grid;grid-template-areas:'winner' 'board' 'alert';grid-template-rows:1fr 9fr 1fr;justify-items:center;align-items:center;grid-gap:2px}.checkers .game .winner{grid-area:winner;display:grid;grid-template-areas:'text recycle';grid-template-columns:3fr 1fr;grid-template-rows:1fr;align-self:stretch;justify-self:stretch}.checkers .game .winner .text{grid-area:text;font-size:25px;font-weight:bold;font-family:sans-serif;color:white}.checkers .game .winner .recycle{grid-area:recycle;background-image:url(/project/static/media/recycle.c3b390de.png);background-size:contain;background-repeat:no-repeat;background-position:right}.checkers .game .board{display:grid;grid-template-columns:repeat(8, 80px);grid-template-rows:repeat(8, 80px)}.checkers .game .board .cell{position:relative}.checkers .game .board .cell .color{position:absolute;width:100%;height:100%;left:0;top:0}.checkers .game .board .cell .character{position:absolute;width:100%;height:100%;left:0;top:0}.checkers .game .board .cell .brown{background-image:url(/project/static/media/brown.18c25caf.svg);background-size:100%;background-repeat:no-repeat;background-position:center;z-index:0}.checkers .game .board .cell .white{background-image:url(/project/static/media/white.13980704.svg);background-size:100%;background-repeat:no-repeat;background-position:center;z-index:0}.checkers .game .board .king{border:4px solid gold}.checkers .game .board .selected{border:4px solid red}.checkers .game .alert{grid-area:alert;font-size:25px;font-weight:bold;align-self:center;font-family:sans-serif;color:white}

.tetris{grid-area:myContent;display:grid;grid-template-rows:100%;grid-template-columns:100%;justify-items:center;align-items:center}.tetris .board{display:grid;grid-template-rows:repeat(20, 35px);grid-template-columns:repeat(10, 35px)}.tetris .board .cell{border:solid 1px black}

